import LoginForm from "@/components/auth/LoginForm";

export default function LoginPage() {
  return (
    <div className="min-h-screen bg-black text-white flex">
      <div className="flex-1 flex items-center justify-center p-8">
        <LoginForm />
      </div>
      <div className="flex-1 bg-gradient-to-br from-gray-900 to-black hidden lg:flex flex-col items-center justify-center p-12 space-y-8">
        <FeatureCard
          title="AI Powered"
          description="Advanced analytics capabilities"
        />
        <FeatureCard
          title="Best Practice Modeling"
          description="Advanced analytics capabilities"
        />
        <FeatureCard
          title="Reporting Engine"
          description="Insight efficiency you"
        />
      </div>
      <div className="absolute bottom-4 left-0 right-0 text-center text-gray-500 text-xs">
        <p>All rights reserved © 2025 Forecast.</p>
        <p className="mt-1">Created by SysarTx Team.</p>
      </div>
    </div>
  );
}


const FeatureCard = ({ title, description }: { title: string; description: string }) => (
  <div className="w-full max-w-md bg-gray-800/50 border border-gray-700 rounded-lg p-6">
    <h3 className="text-lg font-semibold mb-2">{title}</h3>
    <p className="text-gray-400">{description}</p>
  </div>
);
